.site-footer {
  background: #fff;
  padding: 0;
  text-align: left;
}

.site-footer p {
  margin-bottom: 0.9375rem;
}

.site-footer__title {
  margin-bottom: 0.625rem;
  margin-top: 1.4375rem;
}

.site-footer__logo {
  display: block;
  height: 2.1875rem;
  margin: 0 0 1.25rem;
  width: 2.1875rem;
}

.site-footer__nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.site-footer__nav-item {
  margin: 0 0 0.4375rem 0;
}

.site-footer__nav-item::before {
  content: '';
  display: none;
}

.site-footer__nav-link {
  display: inline;
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 1.125rem;
}

.site-footer__hr {
  border: 1px solid #e5e5e5;
  margin: 1.4375rem 0;
}

.site-footer__modal-button {
  background: #fff;
  border: 0;
  border-radius: 0.9375rem;
  bottom: 0.75rem;
  color: #006e9b;
  font-size: 0.75rem;
  line-height: 1.875rem;
  padding: 0 0.9375rem;
  position: absolute;
  right: 0.75rem;
}

.site-footer__modal-button:visited, .site-footer__modal-button:active {
  border: 0;
}

.site-footer__modal-button:hover, .site-footer__modal-button:focus {
  border: 0;
  box-shadow: 0 0.125rem 0.125rem rgba(178, 178, 178, 0.25);
  color: #009ede;
}

.site-footer__modal-button .icon {
  display: inline-block;
  float: left;
  height: 0.875rem;
  margin-right: 0.3125rem;
  margin-top: 0.4375rem;
  width: 0.75rem;
}

.site-footer__modal .modal__close {
  background: #fff;
  border: 1px solid #006e9b;
  border-radius: 0.9375rem;
  color: #006e9b;
  font-size: 0.75rem;
  height: auto;
  line-height: 1.875rem;
  padding: 0 0.9375rem;
  position: absolute;
  width: auto;
}

.site-footer__modal .modal__close:visited, .site-footer__modal .modal__close:active {
  background: #fff;
  border: 1px solid #006e9b;
  color: #006e9b;
}

.site-footer__modal .modal__close:hover, .site-footer__modal .modal__close:focus {
  background: #fff;
  border: 1px solid #009ede;
  color: #009ede;
}

.site-footer__modal .modal__close .icon {
  display: inline-block;
  float: left;
  height: 1rem;
  margin-right: 0.3125rem;
  margin-top: 0.4375rem;
  width: 1rem;
}

@media print, screen and (min-width: 48em) {
  .site-footer p.site-footer__vat {
    margin: 2.4375rem 0 0;
  }
  .site-footer p.site-footer__copyright {
    line-height: 2.1875rem;
    text-align: right;
  }
  .site-footer__nav-item {
    margin: 0 0 0.375rem 0;
    width: auto;
  }
  .site-footer__modal-button {
    bottom: 2rem;
    right: 1.875rem;
  }
  .site-footer__modal .modal__close {
    bottom: 2rem;
    right: 1.875rem;
    top: auto;
  }
}
